import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

const initialState = {
    newTeam: {},
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTeam() {
		const { teamStore } = this.props.stores;
        teamStore.load(this.props.team);
    }

    back = () => {
        route(`/teams/`, true);
    }

    componentWillMount() {
        this.loadTeam();
    }

	render() {
		const { teamStore } = this.props.stores;
		const { team } = teamStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Team</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
						<h5>{team.name}</h5>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href={`/teams/edit/${team.id}`}
							>
								<i class='fas fa-plus' /> Endre {team.name}
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default TeamsDetail;
