import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		default:
			break;
	}
}

function getWorkoutType(workout) {
	switch (workout.type) {
		case 1:
			return 'Trening';
		case 2:
			return 'Konkurranse';
		case 3:
			return 'Tur';
		default:
			break;
	}
}

const initialState = {
    newDog: {},
};

@observer
class WorkoutDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadWorkout() {
		const { workoutStore } = this.props.stores;
        workoutStore.load(this.props.workout);
    }

    back = () => {
        route(`/workouts/`, true);
    }

    componentWillMount() {
        this.loadWorkout();
    }

	render() {
		const { workoutStore, userStore } = this.props.stores;
		const { workout } = workoutStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Trening</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
						<div class='d-flex mb-2'>
							<div class={`text-center border rounded-circle imageRounded ${getWorkoutColor(workout)}`} style={workout.image ? `background-image: url("${workout.image.s3SmallLink}");` : ''}>
								{!workout.image && <i class={`${getWorkoutIcon(workout)} text-muted mt-3`} style='font-size: 40px;' />}
							</div>
							<div class='flex-grow-1 pl-3'>
								<h5 class='my-0'>{workout.name}</h5>
								{workout.team && userStore.findTeam(workout.team)}<br />
								<small class='text-muted'>
									{util.isoDate(workout.date, false, false, true)}
								</small>
							</div>
						</div>

						{/* {track.gpx && <div style='height: 500px;'>
                            <Map stores={this.props.stores} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>} */}

						<div class='container-fluid'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tachometer-alt ml-2' /> Gj.fart</small>
									<h3>
										{workout.speedAvg}km/t
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'>Type</small>
									<h3>
										{getWorkoutType(workout)}
									</h3>
								</div>
	                        </div>
							<div class='row mt-2'>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-tools' /> Utstyr</small>
									<h3>
										{workout.equipment}
									</h3>
								</div>
								<div class='col text-center'>
									<small class='text-muted'><i class='fas fa-dog ml-2' /> Hunder</small>
									<h3>
										{workout.dogs && <span>{workout.dogs.length}</span>}
									</h3>
								</div>
							</div>
							<div class='row mt-2'>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-road ml-2' /> Distanse</small>
									<h3>
										{workout.distanceKm}km
									</h3>
								</div>
								<div class='col  text-center'>
									<small class='text-muted'><i class='fas fa-mountain ml-2' /> Stigning</small>
									<h3>
										{workout.elevation}m
									</h3>
								</div>
							</div>
						</div>

						<div class='d-flex'>
							<a class='btn btn-primary mt-5 float-right'
								href={`/workouts/edit/${workout.id}`}
							>
								<i class='fas fa-plus' /> Endre {workout.name}
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default WorkoutDetail;
