import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import Map from '../../components/map';

const initialState = {
    newTrack: {},
};

@observer
class TracksDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTrack() {
		const { trackStore } = this.props.stores;
        trackStore.load(this.props.track);
    }

    back = () => {
        route(`/tracks/`, true);
    }

    componentWillMount() {
        this.loadTrack();
    }

	render() {
		const { trackStore } = this.props.stores;
		const { track } = trackStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Runde</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

                <div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
                        <div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={track.image ? `background-image: url("${track.image.s3SmallLink}");` : ''}>
                                {!track.image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3'>
                                <h5>{track.name}</h5>
                                {track.created}
                            </div>
                        </div>

                        {track.gpx && <div style='height: 500px;'>
                            <Map stores={this.props.stores} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>}

                        <div class='container-fluid'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> Distance</small>
                                    <h3>
                                        {track.distanceKm} km
                                    </h3>
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> Elevation</small>
                                    <h3>
                                        {track.elevation} m
                                    </h3>
                                </div>
                            </div>
                        </div>

						<div class='d-flex'>
							<a class='btn btn-primary mt-2 float-right'
								href={`/tracks/edit/${track.id}`}
							>
								<i class='fas fa-plus' /> Endre {track.name}
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default TracksDetail;
